.testimonials {
    background-color: #495e57;
    font-family: 'Karla', sans-serif;
    margin-top: 100px;
    margin-bottom: 100px;
    padding-top: 40px;
    padding-bottom: 100px;
}

.testimonals-title {
    font-family: 'Markazi Text', serif;
    font-size: 64px;
    color: #F4CE14;
}

.testimonials-content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 130px;
}

.review {
    width: 250px;
    height: 225px;
    padding: 15px;
    background-color: #FFFFFF;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
    border-radius: 10px;
}

.stars {
    color: #F4CE14;
}

.review-card {
    display: grid;
    grid-template-columns: repeat(4, auto);
    grid-template-rows: repeat(1, 1fr);
    gap: 25px;
}

.name-image {
    display: flex;
    align-items: center;
    gap: 15px;
    margin-top: 10px;
}

.review-text {
    margin-top: 25px;
    text-align: justify;
    font-size: 14px;
}

.review-image {
    height: 50px;
    width: 47px;
    border-radius: 50%;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
}

.review-name{
    font-size: 14px;
    text-transform: uppercase;
 }

@media screen and (max-width: 1080px) {
    .testimonials {
        margin-top: 0px;
        padding-bottom: 50px;
        margin-bottom: 50px;
    }

    .testimonials-content {
        gap: 30px;
    }

    .review-card {
        grid-template-columns: repeat(2, auto);
        grid-template-rows: repeat(1, 1fr);
    }
}

@media screen and (max-width: 580px) {
    .testimonials {
        margin-top: 0px;
        padding-bottom: 40px;
        margin-bottom: 40px;
    }

    .testimonials-content {
        gap: 20px;
    }

    .review-card {
        grid-template-columns: repeat(1, auto);
        grid-template-rows: repeat(1, 1fr);
    }

    .testimonals-title {
        font-size: 40px;
    }
}
